<template>
  <div
    class="home"
    :class="{
      small: screenWidth < 480,
      min: screenWidth < 700,
      med: screenWidth < 1200,
    }"
  >
    <!--  -->
    <div class="header">
      <div class="title">
        <!--  -->
        <span class="name"></span>
        <p>
          <span class="describe"></span>
          <span class="webUrl" @click="copyUrL"
            >https://subtoph.github.io</span
          >
        </p>

        <!--  -->
        <div class="nav" v-if="screenWidth > 800">
          <p
            v-for="item in navList"
            :class="{ act: actNavIndex == item.naviIndex }"
            :key="item.index"
            @click="changeNavAct(item.naviIndex)"
          >
            {{ item.name }}
          </p>
        </div>
        <!--  -->
        <div class="nav" v-else>
          <span
            class="iconfont icon-24px"
            @click="showNavlist = !showNavlist"
          ></span>
        </div>
        <!--  -->
        <div class="navlist" v-if="showNavlist && screenWidth < 800">
          <p
            v-for="item in navList"
            :class="{ act: actNavIndex == item.naviIndex }"
            :key="item.index"
            @click="changeNavAct(item.naviIndex)"
          >
            {{ item.name }}
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="contentBox">
      <!--  -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return {
      showNavlist: false, //
      screenWidth: "", //
      actNavIndex: "", //
      navList: [
        //
        {
          name: "",
          naviIndex: "notes",
        },
        {
          name: "",
          naviIndex: "about",
        },
      ],
    };
  },
  created() {
    // 
    this.actNavIndex = window.location.href.split("#/")[1];
  },
  mounted() {
    this.monitorWidth(); //
  },
  methods: {
    // naviIndex
    changeNavAct(naviIndex) {
      this.actNavIndex = naviIndex; //
      this.$router.push({ path: naviIndex }); //
    },
    // 
    monitorWidth() {
      //
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //
        this.screenWidth = document.body.clientWidth;
      });
    },
    //
    copyUrL() {
      // input
      const input = document.createElement("input");
      // body
      document.body.appendChild(input);
      // input
      input.setAttribute("value", "https://subtoph.github.io");
      // 
      input.select();
      // 
      // input
      // Input
      if (document.execCommand("copy")) {
        document.execCommand("copy");
      }
      alert("https://subtoph.github.io"); //
      // 
      document.body.removeChild(input);
    },
  },
};
</script>
<style lang="less" scoped>
.home {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(233, 233, 233);
  .header {
    position: fixed;
    height: 40px;
    background: #fff;
    width: 100%;
    z-index: 99;
    .title {
      width: 80%;
      line-height: 40px;
      margin: 0 auto;
      padding: 0 20px;
      box-sizing: border-box;
      overflow: hidden;
      span {
        display: block;
        float: left;
      }
      p {
        margin: 0;
        float: left;
        margin-left: 30px;
        span {
          float: none;
          line-height: 20px;
          color: #aaa;
          cursor: pointer;
        }
      }
      .name {
        font-weight: 700;
        font-size: 24px;
        font-style: italic;
      }
      .nav {
        float: right;
        font-size: 18px;
        cursor: pointer;
        p {
          margin: 0;
          padding: 0 10px;
          transition: 0.3s;
          &.act {
            color: rgb(40, 140, 255);
            transform: scale(1.1);
          }
        }
        .iconfont {
          font-size: 24px;
        }
      }
      .navlist {
        position: absolute;
        right: 0;
        top: 45px;
        width: 100px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 0px 1px 4px 0px rgb(146, 146, 146);
        p {
          float: none;
          text-align: center;
          cursor: pointer;
          &.act {
            color: rgb(40, 140, 255);
            transform: scale(1.1);
          }
        }
      }
    }
  }
  .contentBox {
    position: absolute;
    background: rgb(0, 63, 136);
    height: 100%;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 10px;
    display: flex;
    box-sizing: border-box;
    transition: 0.3s;
    .content {
      position: relative;
      margin: 0 auto;
      width: 80%;
      background: #fff;
      border-radius: 5px;
      padding: 10px 3px;
      box-sizing: border-box;
    }
  }
}
// 
.home.min .header .name {
  font-size: 16px;
}
// 
.home.med .header .title {
  width: 100%;
  padding: 0 5px;
}
.min .contentBox {
  padding-top: 45px;
  padding-bottom: 5px;
}
// 
.home.med .contentBox .content {
  width: 100%;
}
.med .header .title p {
  margin: 0;
}
.min .header .title p {
  font-size: 12px;
}
// 
.min .header .title .name {
  display: none;
}
</style>
